import service from "auth/FetchInterceptor";
import API_CONSTANTS from "constants/ApiConstant";
import Utils from "utils";
import moment from "moment";
import { DATE_FORMAT_DD_MM_YYYY } from "constants/DateConstant";
import { HEADERS_NAME_FOR_EXPORT } from "constants/CommonConstants";

const employeeExitReport = {};

employeeExitReport.preparedHeadersForPDF = (data = []) =>
  data.map(col => {
    return { key: col.dataIndex, value: HEADERS_NAME_FOR_EXPORT[col.dataIndex] };
  });

employeeExitReport.preparedDataForExport = (data = [], filterVisibleColumns, forExcel = true) => {
  let headers = {};
  let _data = data.map(record => {
    const _item = {
      employeeId: record?.employeeId || "-",
      hrmsId: record?.hrmsId || "-",
      employeeName: record?.employeeName || "-",
      employeeCountry: record.employeeCountry || "-",
      employeeLocation: record?.employeeLocation || "-",
      employeeDateOfJoining: record?.employeeDateOfJoining
        ? moment(record.employeeDateOfJoining).format(DATE_FORMAT_DD_MM_YYYY)
        : "-",
      parentAccountName: record?.parentAccountName || "-",
      reservedForAccountName: record?.reservedForAccountName || "-",
      projectName: record?.projectName || "-",
      domainName: record?.domainName || "-",
      subDomainName: record?.subDomainName || "-",
      allocationStatus: record?.allocationStatus || "-",
      allocationStartDate: record?.allocationStartDate
        ? moment(record.allocationStartDate).format(DATE_FORMAT_DD_MM_YYYY)
        : "-",
      allocationEndDate: record?.allocationEndDate
        ? moment(record.allocationEndDate).format(DATE_FORMAT_DD_MM_YYYY)
        : "-",
      allocationAging: record?.allocationAging || "-",
      employeeTotalExperience: record?.employeeTotalExperience || "-",
      employeeMobileNumber: record?.employeeMobileNumber || "-",
      employeeDesignation: record?.employeeDesignation || "-",
      employeeEmail: record?.employeeEmail || "-",
      functionalGroupName: record?.functionalGroupName || "-",
      subStatus: record?.subStatus || "_",
      subStatusAging: record?.subStatusAging || "-",
      subStatusStartDate: record?.subStatusStartDate
        ? moment(record.subStatusStartDate).format(DATE_FORMAT_DD_MM_YYYY)
        : "-",
      subStatusEndDate: record?.subStatusEndDate ? moment(record.subStatusEndDate).format(DATE_FORMAT_DD_MM_YYYY) : "-",
      cclName: record?.cclName || "-",
      cclEmail: record?.cclEmail || "-",
      employeeRole: record?.employeeRole || "-",
      onBehalfOf: record?.onBehalfOf || "-",
      employeeStatus: record?.employeeStatus || "-",
      employeeInactiveStatusDate: record?.employeeInactiveStatusDate
        ? moment(record?.employeeInactiveStatusDate).format(DATE_FORMAT_DD_MM_YYYY)
        : "-",
      jobAging: record?.jobAging || "",
    };
    if (filterVisibleColumns) {
      Object.keys(_item).forEach(itemKey => {
        if (!filterVisibleColumns[itemKey]) {
          delete _item[itemKey];
        }
      });
    }
    if (forExcel) headers = Utils.makeHeaderDataForExcel(_item);
    return _item;
  });
  if (forExcel) _data.unshift(headers);
  return _data;
};

